.semicircle {
  display: block;
  position: absolute;
  top: 12px;
  left: 12px;
  width: 18px;
  height: 18px;
  overflow: hidden; }
  @media (max-width: 768px) {
    .semicircle {
      top: 10px;
      left: 10px;
      width: 12px;
      height: 12px; } }
  .semicircle:after {
    content: '';
