/*------------- #Button back-to-top --------------*/
.back-to-top {
  position: absolute;
  z-index: 999;
  width: 50px;
  height: 50px;
  display: block;
  fill: #222121;
  stroke: inherit;
  transition: all .3s ease;
  float: right;
  top: 50%;
  right: 0;
  transform: translate(0, -50%); }
  @media (max-width: 980px) {
    .back-to-top {
      bottom: 0;
      top: auto;
      transform: none; } }
  .back-to-top.hidden {
    opacity: 0; }
  .back-to-top:hover {
    fill: #4cc2c0; }

